import * as yup from 'yup';

export const SearchSchema = yup.object().shape({
  image: yup
    .string()
    .matches(
      /^[a-zA-Z--]+(([' -][a-zA-Z-- ])?[a-zA-Z--]*)*$/,
      'Name may contain only letters, apostrophe, dash and spaces. For example Cat, Candy fridge, Gelatine'
    ),
});
